<template>
  <div class="subAccountManagement">
    <v-container
      fluid
      grid-list-xl>

      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-else>
        <v-layout
          row
          wrap>
          <v-flex
            md6
            tag="h3"
            class="mt-0">
            {{ $ln('子帳號管理') }}
          </v-flex>

          <v-flex
            md6
            class="text-xs-right">
            <!-- 每頁顯示 -->
            <div class="d-inline-block mr-3" style="width:150px">
              <v-select
                v-model="selectRowsPerPageItems.selected"
                :items="selectRowsPerPageItems.options"
                :disabled="loadFilterMenuStatus"
                :label="$ln('每頁顯示')"
              />
            </div>
            <!-- 新增子帳號 -->
            <AppendSubAccount @status="appendStatus"/>
          </v-flex>
        </v-layout>

        <div>
          <div
            v-if="parseInt(pagination.totalItems) > 25 && parseInt(selectRowsPerPageItems.selected) > 10 && pages > 0"
            class="text-xs-center pt-2">
            <v-pagination
              v-model="pagination.currentPage"
              :length="pages"
              :total-visible="pagination.totalVisible"
              color="deep-orange darken-1"
              @input="loadNextPage"/>
          </div>

          <div
            v-show="pagination.totalItems"
            class="pb-1">
            <p class="category d-inline-flex">{{ $ln(`共 %s 個子帳號`, pagination.totalItems) }}</p>
          </div>

          <v-data-table
            :headers="subAccountList.headers"
            :items="subAccountList.contents"
            :no-data-text="subAccountList.showNoData"
            :pagination.sync="pagination"
            hide-actions
            class="elevation-1">
            <template
              slot="headerCell"
              slot-scope="{ header }"
              width="1%">
              <span v-text="$ln(header.text)"/>
            </template>

            <template
              slot="items"
              slot-scope="{ item }">
              <td class="text-xs-center" v-if="item.id" width="1%">{{ item.id }}</td>
              <td class="text-xs-center">
                <router-link :to="{ path: '/manage/account-management/sub-account-management/edit', query: { item: item, page: pagination.currentPage, rowsPerPage: selectRowsPerPageItems.selected } }">{{ item.l_no }}</router-link>
              </td>
              <td class="text-xs-center">{{ item.l_name }}</td>
              <td class="text-xs-center">{{ item.status }}</td>
              <td class="text-xs-center">{{ item.is_close }}</td>
              <td class="text-xs-center minWidth20">{{ item.remark }}</td>
            </template>
          </v-data-table>

          <div
            v-show="pagination.totalItems"
            class="pt-2">
            <p class="category d-inline-flex">{{ $ln(`共 %s 個子帳號`, pagination.totalItems) }}</p>
          </div>

          <div
            v-if="pages > 0"
            class="text-xs-center pt-2">
            <v-pagination
              v-model="pagination.currentPage"
              :length="pages"
              :total-visible="pagination.totalVisible"
              color="deep-orange darken-1"
              @input="loadNextPage"/>
          </div>
        </div>
      </div>

    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { common } from '@/utils'
import AppendSubAccount from './Append'

export default {
  name: 'SubAccountManagement',
  components: {
    AppendSubAccount
  },
  data () {
    return {
      // 頁面載入狀態
      loadingStatus: false,
      // 載入篩選選單(每頁顯示、選擇狀態)
      loadFilterMenuStatus: false,
      // 每頁顯示筆數
      selectRowsPerPageItems: {
        selected: 25,
        options: [] // *initLanguage*
        //['5筆', '10筆', '25筆', '50筆'}]
      },
      // 載入資料列表
      subAccountList: {
        showNoData: '', // *initLanguage*
        // showNoData:尚無資料
        headers: [
          { text: '#', align: 'center', sortable: false },
          { text: '帳號', align: 'center', sortable: false},
          { text: '名稱', align: 'center', sortable: false },
          { text: '身份', align: 'center', sortable: false },
          { text: '是否允許登入', align: 'center', sortable: false },
          { text: '備註', align: 'center', sortable: false }
        ],
        contents: []
      },
      pagination: {
        currentPage: 1, // 當前頁數
        rowsPerPage: 25, // 每頁筆數
        totalItems: 0, // 總筆數
        totalVisible: 10 // 顯示最大可見頁數按鈕，其他會用 ... 顯示
      }
    }
  },
  computed: {
    ...mapGetters({
      getSubAccountState: 'account-management/getSubAccountState'
    }),
    pages () {
      if (this.pagination.rowsPerPage == null || this.pagination.totalItems == null) return 0
      return Math.ceil(this.pagination.totalItems / this.pagination.rowsPerPage)
    }
  },
  watch: {
    '$store.state.lang' (value) {
      this.initLanguage()
    },
    getSubAccountState (payload) {
      // 若 API 有當前頁數回傳資料，可設定如下
      // this.pagination.currentPage = payload.result.current_page
      this.pagination.totalItems = payload.result.totalCount
      this.subAccountList.contents = payload.result.list
    },
    // 選擇顯示筆數
    'selectRowsPerPageItems.selected' (value) {
      this.pagination.rowsPerPage = value
    },
    'pagination.rowsPerPage' (rowsPerPage) {
      // 非編輯頁 watch 狀態
      if (!this.$route.query.pageType) {
        // 回第一頁
        this.pagination.currentPage = 1
        this.loadSubAccount()
      }
    }
  },
  created () {
    this.initLanguage()

    // 載入篩選選單(開始)
    this.loadFilterMenuStatus = true
    // 編輯頁跳回列表-載入記憶頁數
    if (sessionStorage.getItem('account.list.page') && this.$route.query.pageType) this.pagination.currentPage = parseInt(sessionStorage.getItem('account.list.page'))
    else this.pagination.currentPage = 1
    // 編輯頁跳回列表-載入記憶每頁筆數
    if (sessionStorage.getItem('account.list.rowsPerPage') && this.$route.query.pageType) {
      this.selectRowsPerPageItems.selected = parseInt(sessionStorage.getItem('account.list.rowsPerPage'))
      this.pagination.rowsPerPage = parseInt(sessionStorage.getItem('account.list.rowsPerPage'))
    }
    this.loadSubAccount()
    // 載入狀態(開始)
    this.loadingStatus = true

    setTimeout(() => {
      // 清除 pageType
      this.clearQuery('pageType')
      // 載入篩選選單(結束)
      this.loadFilterMenuStatus = false
    }, 1000)
  },
  methods: {
    ...mapActions({
      getSubAccount: 'api/getSubAccount'
    }),
    // 載入子帳號列表
    loadSubAccount () {
      let params = {
        page: this.pagination.currentPage,
        perPage: this.pagination.rowsPerPage
      }
      try {
        this.getSubAccount(params)
          .then(res => {
            if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
              if (res.response.status === 401) return
              // 錯誤訊息
              if (res.response.data.hasOwnProperty('errors')) common.errorMsgHandler(res.response.data.errors)
              else this.$toastr.error(`${this.$ln('取得子帳號列表失敗')}`)
              console.error(res.response)
            } else if (res.status === 200) { // success
              // http 200 回應錯誤訊息
              if (res.data.hasOwnProperty('errors')) {
                console.error('response 200 error: ', res.data)
              } else if (res.data.hasOwnProperty('result')) {
                // console.log(`取得子帳號列表成功`)
              }
            }
            // 載入狀態(結束)
            this.loadingStatus = false
          })
          .catch(err => {
            console.error(err)
            // 載入狀態(結束)
            this.loadingStatus = false
          })
      } catch (error) {
        this.$toastr.error(`${this.$ln('站台資料錯誤，請聯絡客服')}`)
        console.error(error)
      }
    },
    // 載入下一頁
    loadNextPage (page) {
      // 若 API 有當前頁數回傳資料，可註解以下
      this.pagination.currentPage = page
      this.loadSubAccount()
    },
    appendStatus (status) {
      if (status) this.loadSubAccount()
    },
    /**
     * 清除 query 值
     * @param key
     **/
    clearQuery (key) {
      let query = Object.assign({}, this.$route.query)
      delete query[key]
      this.$router.replace({ query })
    },
    // 初始語系
    initLanguage () {
      this.selectRowsPerPageItems.options = [
        // { text: '1筆', value: 1 },
        { text: this.$ln('5筆'), value: 5 },
        { text: this.$ln('10筆'), value: 10 },
        { text: this.$ln('25筆'), value: 25 },
        { text: this.$ln('50筆'), value: 50 }
      ]
      this.subAccountList.showNoData = this.$ln('尚無資料')
    }
  }
}
</script>

<style lang="scss">
  .v-progress-circular {
    margin: 1rem
  }
</style>
